.ui-overlaypanel {
    padding: 0;
    margin: 0;
    position: absolute;
    display: none;
}

.ui-overlaypanel-content {
    padding: 0.5em 1em;
}

.ui-overlaypanel-close {
    position: absolute;
    top: -.5em;
    right: -.5em;
    border-radius: 100%;
}


